<template>
    <div>
        <div v-if="ready">

            <!-- <button type="button" class="btn btn-sm btn-dark btn-block my-1" @click="$router.push('/absen?type=IN&target=Office')">WFO</button>
            <button type="button" class="btn btn-sm btn-dark btn-block my-1" @click="$router.push('/absen/out?type=OUT')">Out</button> -->
            <!-- MODAL -->
            <!-- <button type="button" class="btn btn-sm btn-dark btn-block my-1" @click="$router.push('/absen?type=IN&target=wfo')">WFO</button> -->
            
            <div v-if="modalAbsen" @click="modalAbsen=false" style="position:fixed;width:100vw;height:100vh;left:0;top:0;z-index:100000;background:black;opacity:0.5;"></div>
            <div v-if="modalAbsen" style="position: fixed;top: 50%;left: 50%;transform: translateX(-50%) translateY(-50%);z-index:100005;min-width:90vw;">
                <div class="rounded-lg shadow p-3 bg-white animate__animated animate__bounceIn">
                    <button type="button" class="btn btn-sm btn-dark btn-block my-1" @click="$router.push(`/absen/out?latitude=${users.data.latitude}&longitude=${users.data.longitude}&type=IN&target=Home`)">WFH</button>
                    <button type="button" class="btn btn-sm btn-dark btn-block my-1" @click="$router.push(`/absen?latitude=${users.data.latitude}&longitude=${users.data.longitude}&type=IN&target=Office`)">WFO</button>
                    <button type="button" class="btn btn-sm btn-dark btn-block my-1" @click="$router.push(`/absen/out?latitude=${users.data.latitude}&longitude=${users.data.longitude}&type=IN&target=Trip`)">Perjalanan Dinas</button>
                </div>
            </div>
                <!-- MODAL -->
            <div style="height:40vh;" class="bg-yellow-500" v-if="users">
                <div class="p-3 text-xs">
                    <button type="button" class="btn btn-sm btn-dark  float-right mt-4 rounded-circle px-3"><span class="typcn typcn-user text-3xl"></span></button>
                    <p class="">Selamat Datang!</p>   
                    <p class="mt-1 font-semibold text-lg">{{users.data.name}}</p>   
                    <p class="mt-1 font-semibold ">PT. {{users.data.entity}}</p>   
                </div>
                <div class="p-3 text-xs">
                    <div style="height:100px;"
                    class="shadow rounded-t-lg p-3 bg-white"
                    >
                        <div class="row">
                            <div class="col-9">
                                <p class="font-semibold text-sm mb-1">CLOCK IN 
                                    <span class="" :class="users.data.clockInStatus=='NORMAL'?'text-green-400':'text-red-400'">({{users.data.clockInStatus?users.data.clockInStatus:'-'}})</span>
                                </p>
                                <p class="font-semibold">
                                    Terjadwal : {{users.data.clockInTime}}
                                </p>
                                <p class="font-semibold">
                                    Check-In pada : {{users.data.employeeClockIn?users.data.employeeClockIn.split(' ')[1]:'-'}}
                                </p>
                            </div>
                            <div class="col-3">
                                <button type="button" class="btn btn-sm btn-success rounded-circle p-2 px-3 mt-4" v-if="users.data.clockInStatus=='NORMAL'"><span class="typcn typcn-tick"></span></button>
                                <button type="button" class="btn btn-sm btn-danger rounded-circle p-2 px-3 mt-4" v-else-if="users.data.clockInStatus=='LATE'"><span class="typcn typcn-tick"></span></button>
                                <button type="button" class="btn btn-sm btn-dark rounded-circle p-2 px-3 mt-4" v-else><span class="typcn typcn-tick"></span></button>
                            </div>
                        </div>
                    </div>
                    
                    <div style="height:100px;"
                    class="shadow rounded-b-lg p-3 mt-2 bg-white"
                    >
                        <div class="row">
                            <div class="col-9">
                                <p class="font-semibold text-sm mb-1">CLOCK OUT 
                                    <span class="" :class="users.data.clockOutStatus=='NORMAL'?'text-green-400':'text-red-400'">({{users.data.clockOutStatus?users.data.clockOutStatus:'-'}})</span>
                                </p>
                                <p class="font-semibold">
                                    Terjadwal : {{users.data.clockOutTime}}
                                </p>
                                <p class="font-semibold">
                                    Check-Out pada : {{users.data.employeeClockOut?users.data.employeeClockOut.split(' ')[1]:'-'}}
                                </p>
                            </div>
                            <div class="col-3">
                                <button type="button" class="btn btn-sm btn-success rounded-circle p-2 px-3 mt-4" v-if="users.data.clockOutStatus=='NORMAL'"><span class="typcn typcn-tick"></span></button>
                                <button type="button" class="btn btn-sm btn-danger rounded-circle p-2 px-3 mt-4" v-else-if="users.data.clockOutStatus=='LATE'"><span class="typcn typcn-tick"></span></button>
                                <button type="button" class="btn btn-sm btn-danger rounded-circle p-2 px-3 mt-4" v-else-if="users.data.clockOutStatus=='EARLY OUT'"><span class="typcn typcn-tick"></span></button>
                                <button type="button" class="btn btn-sm btn-dark rounded-circle p-2 px-3 mt-4" v-else><span class="typcn typcn-tick"></span></button>
                            </div>
                        </div>
                    </div>
                    <div class="p-2">
                        <!-- <p class="float-right">Lihat Semua</p> -->
                        <p class="font-semibold text-lg">Laporan Kehadiran</p>
                        <div class="p-3">
                            <div v-for="(item, index) in users.empAttendanceSum" :key="index+'listabsen'" class="">
                                <div class="row shadow rounded-lg mt-2 p-2">
                                    <div class="col-3 bg-orange-500 p-3 rounded-xl text-white">
                                        <p class="text-lg text-center font-semibold">{{item.attendance_day}}</p>
                                        <p class="text-lg text-center font-semibold mt-3">{{bulan[parseInt(item.attendance_month)-1]}}</p>
                                    </div>
                                    <div class="col-5">
                                        <p class="">
                                            Clock-In
                                        </p>
                                        <p class="font-semibold">
                                            {{item.clock_in_time?item.clock_in_time:'-'}}
                                        </p>
                                        <hr class="my-2">
                                        
                                        <p class="">
                                            Clock-Out
                                        </p>
                                        <p class="font-semibold">
                                            {{item.clock_out_time?item.clock_out_time:'-'}}
                                        </p>
                                    </div>
                                    <div class="col-4">
                                        <button type="button" class="btn btn-sm btn-success btn-block rounded-xl text-xs mt-3" v-if="item.clock_in_status=='NORMAL'">NORMAL</button>
                                        <button type="button" class="btn btn-sm btn-danger btn-block rounded-xl text-xs mt-3" v-else>LATE</button>
                                        <hr class="my-1">
                                        <button type="button" class="btn btn-sm btn-success btn-block rounded-xl text-xs mt-3" v-if="item.clock_out_status=='NORMAL'">NORMAL</button>
                                        <button type="button" class="btn btn-sm btn-danger btn-block rounded-xl text-xs mt-3" v-else-if="item.clock_out_status=='EARLY OUT'">EARLY OUT</button>
                                        <button type="button" class="btn btn-sm btn-dark btn-block rounded-xl text-xs mt-3" v-else>-</button>
                                    </div>
                                </div>
                            </div>
                            <div style="height:200px;"> </div>
                        </div>
                    </div>
                </div>
            </div>
            <div style="position:fixed;bottom:0;left:0;z-index:1000;width:100vw;" class=""> 
                <div class="row justify-content-center align-content-center">
                <div  class="col-4 border-t-2 border-gray-200 bg-white  text-gray-600 hover:text-blue-500 hover:font-semibold">
                        <p class=" pb-1 text-xl text-center"><span class="typcn typcn-home"></span></p>
                        <p class="text-center text-xs" style="font-size:10px;">Beranda</p>
                </div>
                <div class="col-3 bg-white   text-gray-600 hover:text-blue-500 hover:font-semibold" @click="goAbsen">
                    <div class="text-center " style="position: absolute;top: 10%;left: 50%;transform: translateX(-50%) translateY(-50%);z-index:10005;">
                        <div class="rounded-circle pb-3  shadow"
                        :class="absenStatus=='kosong'?'bg-gray-300':absenStatus=='clockin'?'bg-green-400 ':absenStatus=='clockout'?'bg-blue-600 text-white':'bg-white'"
                        style="width:60px;height:60px;">
                                <p class=" pt-2 text-xl text-center"><span class="typcn typcn-code"></span></p>
                                <p class="text-center text-xs" style="font-size:10px;">{{absenStatus=='kosong'?'bg-gray-300':absenStatus=='clockin'?'In':absenStatus=='clockout'?'Out':'-'}}</p>
                        </div>
                    </div>
                    <div class="text-center " style="position: absolute;top: 10%;left: 50%;transform: translateX(-50%) translateY(-50%);z-index:10000;">
                        <div class="rounded-circle pb-3 bg-transparent border-b-2 border-gray-600" style="width:90px;height:90px;">
                        </div>
                    </div>
                </div>
                <div @click="$router.push('/profile')" class="col-4 border-t-2 border-gray-200 bg-white  text-gray-600 hover:text-blue-500 hover:font-semibold">
                        <p class=" pb-1 text-xl text-center"><span class="typcn typcn-user"></span></p>
                        <p class="text-center text-xs" style="font-size:10px;">Profile</p>
                </div>
                </div>
            </div>
        
        </div>
    </div>
</template>
<script>
import axios from 'axios';
import autophp from '@/plugins/auto';
let sdb = new autophp('ci');
export default{
    data(){
        return{
            users:null,
            modalAbsen:false,
            ready:false,
            absenStatus:'kosong',
            bulan:['Jan','Feb','Mar','Apr','Mei','Jun','Jul','Agust','Sept','Okt','Nov','Des']
        }
    },
    methods: {
        goAbsen(){
            if(this.absenStatus=='kosong'){
                alert('Schedule belum ada!');
            }else if(this.absenStatus=='clockin'){
                this.modalAbsen=true;
            }else if(this.absenStatus=='clockout'){
                this.$router.push('/absen/out?type=OUT')
            }else if(this.absenStatus=='done'){
                alert('Anda sudah melakukan absen hari ini');
            }
            this.$forceUpdate();
        },
        formatTgl(tgl,pattern="YYYY-MM-DD") {
            return this.$datefns.format(
                new Date(tgl),
                pattern
            );
        },
    },
    layout:"app_admin",
    async mounted(){
        if(localStorage.getItem('users')){

        }else{
            this.$router.push('/')
        }
        let users=JSON.parse(sdb.scramble(localStorage.getItem('users')));
        await axios.get('https://officialapp.my.id/api/bridge/attendance_status/'+users.nrp).then(res=>{
            console.log('res',res)
            this.users=res.data
            localStorage.setItem('absenStatus',JSON.stringify(res.data));
            this.$store.state.absenStatus=res.data;
            this.ready=true;
            this.$forceUpdate();
            if(this.users['workSchedule']){
                this.absenStatus='clockin'
                if(this.users['data']['isClockIn']){
                    this.absenStatus='clockout'
                    if(this.users['data']['isClockOut']){
                        this.absenStatus='done'
                    }
                }
            }
        });
        let fd=new FormData();
        fd.append('id_user',users.nrp);
        await axios.post('https://wajah.officialapp.my.id/api/face/check',fd).then(res=>{
            console.log('check wajah',res.data)
            if(res.data.status==false){
                this.$router.push('/daftar-wajah')
            }
        })
        if( /Android|webOS|iPhone|iPad|iPod|BlackBerry|IEMobile|Opera Mini/i.test(navigator.userAgent) ) {
            // some code..
            }else{
            setTimeout(() => {
                this.$router.push('/404')
            }, 1000);
        }
        function appendLocation(location, verb) {
        }
        if ("geolocation" in navigator) {
            sdb.alert('Gps Aktif','bg-green-400');
            navigator.geolocation.getCurrentPosition(function (location) {
                appendLocation(location, "fetched");
            });
            watchId = navigator.geolocation.watchPosition(appendLocation);
        } else {
            sdb.alert("Geolocation API not supported.");
        }
    }
    
}
</script>